import {createReducer} from '../utils/reducerUtils.js';

import {INCREMENT, DECREMENT} from './actionTypes.js';

const initialState = {count : 0};

const ACTION_HANDLER = {
	[INCREMENT] : (state, action) => ({
		...state,
		count : state.count + 1
	}),
	[DECREMENT] : (state, action) => ({
		...state,
		count : state.count - 1
	}),
}
export default createReducer(initialState, ACTION_HANDLER);


